define('ember-emojione/-private/utils/constants', ['exports'], function (exports) {
  'use strict';

  var EMOJI_TONES_ARRAY = ['default', 1, 2, 3, 4, 5];

  exports.EMOJI_TONES_ARRAY = EMOJI_TONES_ARRAY;

  var EMOJI_CATEGORIES_ARRAY = ['people', 'nature', 'food', 'activity', 'travel', 'objects', 'symbols', 'flags'];

  exports.EMOJI_CATEGORIES_ARRAY = EMOJI_CATEGORIES_ARRAY;

  var EMOJI_PROP_NAMES_TONE = EMOJI_TONES_ARRAY.map(function (tone) {
    return 'emojiService.emoji__tone_' + tone;
  }).join(',');

  exports.EMOJI_PROP_NAMES_TONE = EMOJI_PROP_NAMES_TONE;

  var EMOJI_PROP_NAMES_TONE_TONE = (function () {
    var dependentKeys = [];

    EMOJI_CATEGORIES_ARRAY.forEach(function (category) {
      EMOJI_TONES_ARRAY.forEach(function (tone) {
        var key = 'emojiService.' + category + '__tone_' + tone;
        dependentKeys.push(key);
      });
    });

    return dependentKeys.join(',');
  })();
  exports.EMOJI_PROP_NAMES_TONE_TONE = EMOJI_PROP_NAMES_TONE_TONE;
});